<script>
  import Datepicker from "svelte-calendar";
  import Autocomplete from "../components/Autocomplete.svelte";
  import { auth } from "../Auth";
  import { get, post } from "../utils";

  export let updateList;

  let promises = Promise.all([
    get("/games", $auth.token),
    get("/activities/statuses", $auth.token),
    get("/friends", $auth.token)
  ]);
  let gameId;
  let isReplay = false;
  let statusId;
  let selectedDate;
  let otherPlayerId;
  const dateFormat = "#{l}, #{F} #{j}, #{Y}";

  async function submit() {
    // Clearing the input field makes the value an empty string which breaks validation
    const createdAt = selectedDate
      ? selectedDate.toISOString().slice(0, 10)
      : null;
    const additionalUserIds = otherPlayerId ? [otherPlayerId] : [];
    await post(
      "/activities",
      { gameId, isReplay, statusId, createdAt, additionalUserIds },
      $auth.token
    );

    updateList();
  }
</script>

<h1>New Activity</h1>

{#await promises}
  <p>...loading</p>
{:then results}
  <form on:submit|preventDefault={submit}>
    <Autocomplete
      items={results[0]}
      labelFieldName="name"
      valueFieldName="id"
      placeholder="Game"
      bind:value={gameId} />

    <label>
      Are you replaying this game?
      <input type="checkbox" bind:checked={isReplay} />
    </label>

    <Autocomplete
      items={results[1]}
      labelFieldName="name"
      valueFieldName="id"
      placeholder="Status"
      bind:value={statusId} />

    <label>
      Did you play with anyone else?
      <select bind:value={otherPlayerId}>
        <option value={undefined}>Select a friend</option>
        {#each results[2] as friend}
          <option value={friend.id}>{friend.name}</option>
        {/each}
      </select>
    </label>

    <label>
      What day did this happen?
      <Datepicker bind:selected={selectedDate} format={dateFormat} />
    </label>
    <button type="submit" disabled={!gameId && !statusId}>Create</button>
  </form>
{:catch error}
  <p style="color: red">{error.message}</p>
{/await}
