<script>
  import { formatDate as utilsFormatDate } from "timeUtils";
  import { auth } from "../Auth";
  import { get } from "../utils";

  let activitiesPromise;

  getActivities();

  export function getActivities() {
    activitiesPromise = get("/activities", $auth.token);
  }

  function formatDate(isoDate) {
    // Since our date comes in utc format we need to create it this way to fix days being off by one due to tz offset
    var parts = isoDate.split("-");
    return utilsFormatDate(
      new Date(parts[0], parts[1] - 1, parts[2]),
      "#{l}, #{F} #{j}, #{Y}"
    );
  }
</script>

<h2>Previous Activities</h2>
{#await activitiesPromise}
  ...loading
{:then activities}
  <ul>
    {#each activities as { users, game, status, ...activity }}
      <li>
        {game.name} was {status.name.toLowerCase()} by you
        {#if users[1]}and {users[1].name}{/if}
        on {formatDate(activity.createdAt)}
      </li>
    {/each}
  </ul>
{:catch error}
  <p style="color: red">{error.message}</p>
{/await}
